@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;500;700;900&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background-color: #eee;
  color: #333;
  position: relative;
  overflow-x: hidden;
  font-family: "Poppins", sans-serif;
  scroll-behavior: smooth;
}

body::after {
  content: "";
  width: 1200px;
  height: 1000px;
  border: 2px solid #aaa;
  position: fixed;
  bottom: 100px;
  z-index: -100;
  left: -400px;
  transform: rotate(120deg);
}

body::before {
  content: "";
  width: 800px;
  height: 1200px;
  border: 2px solid #aaa;
  position: fixed;
  bottom: -400px;
  right: -400px;
  z-index: -100;
  transform: rotate(30deg);
}

::selection {
  background: #ccc;
  color: #000;
}
/* ### */
/* Progress line */
/* ### */
.progress-bar {
  position: fixed;
  top: 0;
  left: 0;
  background: linear-gradient(to right, #ff2981, #4ed955);
  height: 6px;
  z-index: 1000;
}

/* ### */
/* General styles */
/* ### */

a {
  text-decoration: none;
  color: #333;
}

.container {
  max-width: 1200px;
  width: 90vw;
  margin: auto;
}

/* ### */
/* Nav */
/* ### */
.nav {
  padding: 20px 0;
}

.nav a {
  font-size: 1.2rem;
}

.navbar {
  display: none;
  justify-content: space-between;
  width: 100%;
  align-items: center;
}

.nav-btn {
  background: none;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  color: #333;
  font-size: 1.5rem;
  cursor: pointer;
  outline: none;
}

.nav-left {
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-transform: uppercase;
  width: 100%;
}

.nav-left a {
  display: flex;
  align-items: center;
}

.nav-left a span {
  padding-left: 10px;
  letter-spacing: 5px;
  font-weight: 300;
}

.photo {
  width: 38px;
  height: 38px;
  object-fit: cover;
  object-position: top;
  border-radius: 50%;
  display: block;
}

.nav-items-container {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;

  background: #cfcfcff1;

  z-index: 100;
}

.nav-modal-btn {
  background: none;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  color: #eee;
  font-size: 1.5rem;
  cursor: pointer;
  outline: none;
  position: absolute;
  top: 20px;
  right: 5vw;
  z-index: 1000;
}

.word {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: -1;
  font-size: 4rem;
  opacity: 0.2;
  text-transform: uppercase;
  color: #000;
  font-weight: 900;
}

.nav-items {
  list-style-type: none;
  display: flex;
}

.nav-items li a {
  margin: 0 10px;
  padding: 2px 0;
}

.sidebar-items {
  list-style-type: none;
  display: flex;
  position: relative;
}

.sidebar-items li a {
  text-transform: uppercase;
  font-size: 1.5rem;
  font-weight: 500;
  transition: color 0.2s;
}

.sidebar-items li a:hover {
  display: inline;
  border-bottom: 2px solid #ff2981;
  color: #ff2981;
}

.active {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 100%;
}

.nav-items a,
.sidebar-items a {
  position: relative;
  letter-spacing: 2px;
  display: inline-block;
  font-weight: 300;
}
.nav-items li a {
  text-transform: capitalize;
}

.nav-items li a {
  transition: color 0.3s ease;
}
.nav-items li a:hover {
  color: #ff2981;
}
.nav-items li a:hover::after,
.nav-items li a:hover::before {
  animation: expandLineWidth 0.3s ease forwards;
}

.nav-items a::after {
  content: "";
  width: 0%;
  height: 1px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav-items a::before {
  content: "";
  width: 0%;
  height: 1px;
  position: absolute;
  top: 0;
  right: 0;
}

.github,
.instagram {
  padding: 5px;
}

.github:hover,
.instagram:hover {
  cursor: pointer;
  color: #ff2981;
}
@keyframes expandLineWidth {
  0% {
    width: 0%;
    background: #eee;
  }
  100% {
    width: 100%;
    background: #ff2981;
  }
}

/* ### */
/* Header*/
/* ### */
.header {
  width: 100vw;
  height: 550px;
  display: grid;
}

.header-title {
  text-align: center;
  text-transform: uppercase;
  grid-area: 1 / 1 / 2 / 2;
  align-self: center;
}

.title b {
  font-size: 3.7rem;
  letter-spacing: 5px;
  position: relative;
}

.title b::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 40%;
  background-color: #4ed955;
  z-index: -1;
}

.text-gray {
  color: #666666;
  letter-spacing: 3px;
  font-size: 1.4rem;
}

/* ### */
/* About */
/* ### */

.about {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  padding: 200px 0;
}

.about-img {
  grid-row: 1 / 2;
  grid-column: 10/ 13;
}

.about-img img {
  width: 100%;
  object-fit: cover;
}

.about-content {
  grid-column: 1/ 9;
  grid-row: 1 / 2;
  position: relative;
}

.about-title {
  font-size: 8rem;
  text-transform: uppercase;
  color: rgba(0, 0, 0, 0.075);
  font-weight: 900;
  position: absolute;
  top: 0;
  left: 0;
  line-height: 85px;
  z-index: -1;
}

.about-content h3 {
  color: rgb(0, 0, 0);
  font-size: 1.5rem;
  margin: 30px 0;
}

.about-content p {
  margin: 10px 0;
}

.about-content span {
  color: #ff2981;
}

.about-icons {
  margin: 10px 0;
}

.icon {
  color: #ff2981;
  background-color: #000;
  font-size: 3rem;
  border-radius: 50%;
  padding: 10px;
  margin-right: 10px;
}

.about-contact {
  font-size: 1.3rem;
}

.about-email {
  font-size: 1.1rem;
  font-weight: 500;
}

.about-email span {
  font-size: 1.1rem;
  font-weight: 500;
  color: #888;
}

/* ### */
/* Skills */
/* ### */
.skills {
  position: relative;
  display: grid;
  justify-items: center;
  margin: 100px auto;
}

.skills-header {
  grid-area: 1 / 1 / 2 / 2;
  text-align: center;
}

.skills-title {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  font-size: 8rem;
  text-transform: uppercase;
  color: rgba(0, 0, 0, 0.075);
  font-weight: 900;
  line-height: 85px;
  z-index: -1;
}

.skills-subtitle {
  color: rgb(0, 0, 0);
  font-size: 1.5rem;
  padding: 25px 0;
}

.skills-container {
  display: flex;
  margin: 50px auto 10px;
  cursor: pointer;
  grid-area: 2/ 1 / 3 / 2;
}

.skills-icon {
  color: #4ed955;
  background-color: #000;
  font-size: 4rem;
  border-radius: 5px;
  padding: 10px;
  margin: 0 10px;
}

.skills-tab {
  grid-area: 3 /1 / 4/ 2;
  padding: 40px 0;
}

.skills-tab h3 {
  font-size: 2rem;
  text-align: center;
}

.skills-tab ul {
  text-transform: capitalize;
  list-style: none;
}

.skills-tab ul li {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  font-size: 1.4rem;
}

.skills-tab-icon {
  color: #ff2981;
  margin-right: 10px;
}

footer {
  width: 100%;
  text-align: center;
}

footer h5 {
  padding: 20px;
}
/* ### */
/* Media querys */
/* ### */

@media only screen and (min-width: 920px) {
  .navbar {
    display: flex;
  }
  .sidebar {
    display: none;
  }
}

@media only screen and (max-width: 500px) {
  .nav-left a span {
    letter-spacing: 2px;
  }
}

